@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;1,100;1,300;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  /* text-align: center; */
  font-family: 'Roboto', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.TaskList-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.Task {
	background-color: #ebecf0;
	border-radius: 10px;
	width: 272px;
	align-self: flex-start;
	margin: 2rem 2.5%;
}

.Task-title {
	display: flex;
	justify-content: space-between;
	margin: 0px 0.8rem;
	font-size: 16px;
	font-weight: 700;
}

.Task-title-form {
	display: flex;
	justify-content: space-between;
	margin: 0.5em 0.8rem;
}

.Task-title-form button {
	outline: none;
	border: none;
	cursor: pointer;
}

.Task-title-form input {
	outline: none;
	width: 200px;
	border: none;
	padding: 0.5em 1em;
	border-radius: 6px;
	font-size: 16px;
	font-weight: 700;
	outline: none;
	font-family: 'Roboto', sans-serif;
	resize: none;
}

.Task-add {
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	margin: 0px 0.6rem 0.5rem;
}

.Task-add textarea {
	font-family: 'Roboto', sans-serif;
	outline: none;
	border: none;
	border-radius: 6px;
	padding: 5px;
	/* resize: none; */
}

.Task-add button {
	border: none;
	outline: none;
	font-size: 20px;
	cursor: pointer;
}

.Task-options {
	display: flex;
	justify-content: center;
	flex-direction: row;
	padding: 10px 0px;
}

.Task-options button {
	outline: none;
	border: none;
	cursor: pointer;
}

.Todo {
    background-color: white;
    border-radius: 5px;
    margin: 0.6rem;
    padding: 8px 0.5rem;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.Todo:hover {
    cursor: pointer;
}

.Todo-text {
    font-size: 14px;
    margin: 0px;
}

.Todo button {
    outline: none;
    border: none;
    background-color: white;
    cursor: pointer;
}
